<template>
  <div id="app">
    <div>
      <el-container>
        <el-header style="height: 140px; border-bottom: 1px solid #eee">

          <div class="header-image">
            <el-image style="height: 80px;" :src="logoImageSrc"></el-image>
          </div>
          <div class="menuControl"
            @mouseenter="changebgcolor"
            @mouseleave="clearcolor"
            @click="collapseStatus"
            :style="{ 'background-color': collapsebgcolor }"
          >
            <i class="el-icon-s-fold" v-show="block"></i>
            <i class="el-icon-s-unfold" v-show="toggle"></i>
          </div>
        </el-header>
        <el-container>
          <el-aside class="aside-menu" :width="collapseSize">
            <!-- <el-radio-group v-model="isCollapse">
              <el-radio-button :label="false">展开</el-radio-button>
              <el-radio-button :label="true">收起</el-radio-button>
            </el-radio-group> -->
            <el-menu
              router
              default-active="$router.currentRoute.path"
              :default-active="currentMenu"
              class="el-menu-vertical-demo"
              @select="handleSelect"
              @open="handleOpen"
              @close="handleClose"
              :collapse="isCollapse"
            >
              <el-menu-item index="/">
                <i class="el-icon-s-home"></i>
                <span class="home-class" slot="title">Home</span>
              </el-menu-item>
              <el-submenu
                :index="item.id"
                v-for="(item, index) in items"
                :key="index"
              >
                <template slot="title">
                  <i class="el-icon-suitcase"></i>
                  <span class="el-menu-span1" slot="title">{{
                    item.name
                  }}</span>
                </template>
                <el-submenu
                  :index="sub.id"
                  v-for="(sub, index) in item.subitem"
                  :key="index"
                >
                  <span class="el-menu-span2" slot="title">{{ sub.name }}</span>
                  <el-menu-item
                    :index="sub.id"
                    v-for="(sub, index) in sub.subitem"
                    :key="index"
                    @click="clickHandle(sub)"
                  >
                    <span class="el-menu-span3">{{ sub.name }} </span>
                  </el-menu-item>
                </el-submenu>
              </el-submenu>
            </el-menu>
          </el-aside>

          <el-container>
            <el-main>
              <!-- 路由 -->
              <router-view />
            </el-main>
            <el-footer>
              <div style="text-align: center; position:relative;">
                <p style="font-style:italic; font-size: small;color: #666; ">
                  如果对您有帮助给个<a
                    href="https://github.com/zhufuyi/sponge"
                    target="_blank"
                    >star⭐</a
                  >
                  ，欢迎加入
                  <el-button
                    type="text"
                    style="font-size: small;"
                    @click="dialogTableVisible = true"
                    >go sponge微信交流群。</el-button
                  >
                </p>

                <el-dialog
                  title="加微信进群"
                  :visible.sync="dialogTableVisible"
                >
                  <el-image
                    style="height: 320px;"
                    :src="wechatImageSrc"
                  ></el-image>
                </el-dialog>
              </div>
            </el-footer>
          </el-container>
        </el-container>
      </el-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      isCollapse: false,
      // collapseBtnClick: false,
      collapseSize: "300px",
      toggle: false, //第二个图标默认隐藏
      block: true, //默认显示第一个图标
      collapsebgcolor: "",

      dialogTableVisible: false,
      logoImageSrc: require("@/assets/go-sponge-logo.png"),
      wechatImageSrc: require("@/assets/wechat.jpg"),
      currentMenu: "/ui",
      items: [
        {
          name: "SQL",
          id: "1",
          subitem: [
            {
              name: "Web类型",
              id: "1-1",
              subitem: [
                {
                  name: "创建web项目",
                  id: "/ui/web-http",
                },
                {
                  name: "生成handler CRUD代码",
                  id: "/ui/web-handler",
                },
              ],
            },
            {
              name: "RPC类型",
              id: "1-2",
              subitem: [
                {
                  name: "创建rpc项目",
                  id: "/ui/micro-rpc",
                },
                {
                  name: "生成service CRUD代码",
                  id: "/ui/micro-service",
                },
                {
                  name: "生成protobuf描述信息",
                  id: "/ui/micro-protobuf",
                },
              ],
            },
          ],
        },
        {
          name: "Protobuf",
          id: "2",
          subitem: [
            {
              name: "Web类型",
              id: "2-1",
              subitem: [
                {
                  name: "创建web项目",
                  id: "/ui/web-http-pb",
                },
                {
                  name: "创建rpc gateway项目",
                  id: "/ui/micro-rpc-gw-pb",
                },
              ],
            },
            {
              name: "RPC类型",
              id: "2-2",
              subitem: [
                {
                  name: "创建rpc项目",
                  id: "/ui/micro-rpc-pb",
                },
              ],
            },
          ],
        },
        {
          name: "Public",
          id: "3",
          subitem: [
            {
              name: "Yaml",
              id: "3-1",
              subitem: [
                {
                  name: "生成config代码",
                  id: "/ui/yaml-config",
                },
              ],
            },
            {
              name: "Dao",
              id: "3-2",
              subitem: [
                {
                  name: "生成dao CRUD代码",
                  id: "/ui/web-dao",
                },
              ],
            },
            {
              name: "Model",
              id: "3-3",
              subitem: [
                {
                  name: "生成model代码",
                  id: "/ui/web-model",
                },
              ],
            },
            {
              name: "RPC client",
              id: "3-4",
              subitem: [
                {
                  name: "生成rpc client代码",
                  id: "/ui/micro-rpc-cli",
                },
              ],
            },
          ],
        },
      ],
    };
  },
  // 刷新展开折叠菜单
  watch: {
    $route(e) {
      this.currentMenu = e.path;
    },
  },
  created() {
    this.currentMenu = this.$route.path;
    window.addEventListener("resize", this.handleResize);
  },
  beforeDestroy: function() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize(event) {
      this.fullWidth = document.documentElement.clientWidth;
    },
    handleSelect(key, keyPath) {
      // this.currentMenu = key;
    },
    handleOpen(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // this.currentMenu = key;
      // console.log(key, keyPath);
    },
    clickHandle(sub) {
      // console.log(sub.id, sub.name);
    },

    collapseStatus() {
      // this.collapseBtnClick = this.isCollapse;
      this.toggle = !this.toggle;
      this.block = !this.block;
      this.isCollapse = !this.isCollapse;
      if (this.isCollapse) {
        this.collapseSize = "0px";
      } else {
        this.collapseSize = "300px";
      }
    },

    // 鼠标移动上更换背景色
    changebgcolor() {
      // index:鼠标移上的元素的索引值
      this.collapsebgcolor = "#409EFF";
    },
    //鼠标移出，还原颜色
    clearcolor() {
      this.collapsebgcolor = "";
    },
  },
};
</script>

<style scoped>
#app {
  margin: 0px;
  padding: 0px;
  background: url("assets/body-background.png") repeat;
}

.menuControl{
  width: 25px;
}

.aside-menu {
  margin: 0px;
  padding: 0px;
  background-color: #f8f8f8;
}

.aside-menu /deep/ .el-menu {
  background-color: #f8f8f8;
}

.el-menu-vertical-demo:not(.el-menu--collapse) {
  /* width: 280px; */
  min-height: 880px;
}
.el-submenu .el-menu-item {
  font-family: "Microsoft YaHei UI";
  padding: 0 15px;
}

.el-col {
  /* border-radius: 4px; */
  background: #99a9bf;
  background: url("assets/body-background.png");
}

.home-class {
  font-weight: bold;
  font-size: 15px;
}

.el-menu-span1 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  font-size: 15px;
}
.el-menu-span2 {
  font-family: "Microsoft YaHei UI";
  font-weight: bold;
  padding: 0 15px;
  font-size: 14px;
}
.el-menu-span3 {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  padding: 0 15px;
}

.header-image {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  padding-top: 20px;
  padding-left: 15%;
}
</style>



